<template>
  <div>
    <div class="bar-box" :class="{'show':scrollTops>700}">
      <span >
        <i v-if="domArr" v-for="(g,i) in vData" @click="navTo(topArr[i])" ><b class="no-b" style="width: 24px; display: inline-block">{{g.name}}</b></i>
      </span>
      <span>
        <i v-if="type==1" @click="$router.push('/index')">首页</i>
        <i >
          <b class="iconfont icon-guanzhu no-b" style="font-size: 24px"></b>
          <div class="bar-box-content"  >
            <em v-if="!loginStatus"><a href="javascript:void(0)" @click="$router.push({ name: 'Login' })" class="color_a">登录</a>查看我的关注</em>
            <em style="color: #fff; background: #7bbe2b; padding: 10px" v-else @click="$router.push('/User?nav=collection&subNav=collectionList')">我的关注</em>
          </div>
        </i>
        <i><b class="no-b" style="width: 24px; display: inline-block">银犁商城</b>
          <div class="bar-box-content"  >
            <img src='../static/images/attention.png'>
          </div>
        </i>
        <i @click="navTo(0)" class="f12 font-st">Top</i>
      </span>


    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  export default{
    name:"Bar",
    props:["domArr","type"],
    data(){
      return {
        vData:[
          {name:"蔬菜",value:"1"},
          {name:"水果",value:"1"},
          {name:"海鲜水产",value:"1"},
          {name:"肉、蛋类",value:"1"},
          {name:"干货零食",value:"1"},
          {name:"厨房调料",value:"1"},
          {name:"速冻食品",value:"1"},
          {name:"粮油",value:"1"},
          {name:"饮品",value:"1"},
        ],
        scrollTops:0
      }
    },
    computed:{
      ...mapState(['loginStatus']),
      topArr(){
        let topArr=[];
        let doms=this.domArr;
        for (var j in  doms){
          topArr.push(doms[j].offsetTop-100)
        }
        return topArr
      }
    },
    created(){
      let that=this;
      window.onscroll = function(e){
        that.scrollTops=this.pageYOffset;
      }

    },
    methods:{
      navTo(d){
        window.scrollTo(0,d)
      }
    },


  }
</script>
<style scoped lang="stylus">
  $wh = 44px
  $color = #7bbe2b
  $color_fff = #fff
  $color_a=#ffc000
  .bar-box{border: 4px solid $color;position: fixed;bottom: 30px;right: 0px;background: white; font-size: 12px; z-index: 9999; opacity: 0;transition: all 0.5s}
  .bar-box i{display: flex;  align-items: center; padding: 10px; width:$wh; height: $wh; text-align: center;box-sizing: border-box; border-bottom: 1px solid $color; cursor: pointer; word-break: break-all;word-wrap: break-word }
  .bar-box span:nth-child(2) i{ color:$color; position: relative;  }
  .bar-box i:hover,.bar-box span:nth-child(2) i:hover{ color:$color_fff ; background:$color }
  .bar-box.show{opacity: 1; }
  .bar-box-content{border: 4px solid $color; background: $color_fff; position: absolute; font-size: 12px;right: 45px; color:#666;display: none; border-radius: 4px; top:0px  }
  .bar-box-content em{width: 50px; display: block; padding: 6px}
  .bar-box span:nth-child(2) i:hover .bar-box-content{display: block}
  .color_a{ color: $color_a }

</style>



