<template>
  <div class="icon-gouwuche-box">
  </div>
</template>
<script>
  import "../static/js/jquery.min.js"
  import "../static/js/jquery.fly.min.js"
  export default {
    name: 'Flyball',
    data () {
      return {}
    },
    computed: {
    },
    methods: {
      drop(el){
        this.$nextTick(()=>{
          addCart()
          function addCart(){
            let flyer=$('<i class="iconfont icon-gouwuche flyer" ></i>'),
              endLeft=$(".cart i.icon-gouwuche").offset().left;
            flyer.fly({
              start:{
                left: el.clientX,  //开始位置（必填）#fly元素会被设置成position: fixed
                top: el.clientY,  //开始位置（必填）
              },
              end:{
                left:endLeft , //结束位置（必填）
                top: 40,  //结束位置（必填）
              },
              onEnd: function(){
                flyer.remove()
              } //结束回调
            });
          }
        });

      },

    }
  }
</script>
<style lang="stylus">
  .flyer{ position: absolute; z-index: 20000;color: #FF4E00}
</style>



